}

.no-padding-side {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.container-fluid .card {
  border: none;
  -webkit-box-shadow: 3px 3px 5px 2px rgba(50, 50, 50, 0.3) !important;
          box-shadow: 3px 3px 5px 2px rgba(50, 50, 50, 0.3) !important;
  margin-bottom: 10px;
}

.card-body {
  padding-top: 5px;
}

.card-title {
  margin-bottom: 0;
  font-size: 14pt;
  color: #2b2b2b;
}

.card-date {
  margin: 0;
  color: #7f7f7f;
  font-size: 14px;
}

.card-place {
  margin: 0;
  color: #000;
  font-size: 12px;
  text-align: left;
  font-weight: 400;
